*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body{
  background-color: #f5f5f5;
  font-family: sans-serif;
}

a{
  text-decoration: none;
  color: inherit;
}

.container{
  padding-inline: 3rem;
}

.btn{
  background-color: white;
  border-radius: 30px;
  color: black;
  font-size: 16px;
  padding: 14px 25px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn img{
  width: 30px;
  margin-left: 10px;
}

.btn.darkbtn{
  background-color: #212EA0;
  color: white;
}

@media (max-width : 1200px){
  .container{
      padding-inline: 1rem;
  }

  .btn{
      padding: 10px 24px;
  }
}