.campus{
    margin: 70px auto;
    text-align: center;
    width: 90%;
}

.gallery{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;
}

.gallery img{
    width: 23%;
    border-radius: 10px;
}

@media(max-width : 800px){
    .gallery{
        flex-wrap: wrap;
    }

    .gallery img{
        width: 48%;
        margin-bottom: 25px;
    }
}