.contact{
    margin: 80px auto;
    max-width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contact-column{
    flex-basis: 40%;
    color: #676767;
}

.contact-column h3{
    color: #000F38;
    font-weight: 500;
    font-size: 25px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.contact-column h3 img{
    width: 35px;
    margin-left: 10px;
}

.contact-column p{
    max-width: 450px;
    list-style: 0.3;
}

.contact-column ul li{
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.contact-column ul li img{
    width: 25px;
    margin-right: 10px;
}

.contact form input, .contact form textarea{
    display: block;
    width: 100%;
    background-color: #EBECFE;
    padding: 15px;
    border: 0;
    outline: 0;
    margin: 15px 5px;
    resize: none;
}

.contact-column span{
    display: block;
    margin: 20px 0;
}

@media(max-width: 700px){
    .contact{
        display: block;
        margin: 40px auto;
    }

    .contact-column{
        padding: 20px;
    }
}