.navigation{     
    width: 100%;
    color: white;
    position: fixed;
    top: 0px;
    left : 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
    padding-block: 8px;
}

.logo{
    width: 200px;
}

.items{
    display: flex;
    gap: 2rem;
    font-size: 18px;
    align-items: center;
}

.darkNav{
    background-color: #212EA0;
    transition: 0.5s;
}

.menuIcon{
    display: none;
    width: 25px;
}

@media (max-width : 1000px){
    .logo{
        width: 140px;
    }

    .items{
        gap: 1rem;
    }
}

@media (max-width : 800px){
    .items{
        position: fixed;
        top: 0;
        right: 0;
        background-color: #212EA0;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        flex-direction: column;
        transition: 0.5s;
        bottom: 0;
        align-items: baseline;
        padding-left: 16px;   
    }

    .menuIcon{
        display: block;
    }

    .HideMobileMenu{
        right: -200px;
    }
}