.testimonials{
    margin: 80px auto;
    padding: 0 80px;
    position: relative;
}

.next-btn, .back-btn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    padding: 15px;
    width: 50px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #212EA0;
}

.back-btn{
    right: auto;
    left: 0;
}

.slider{
    overflow: hidden;
}

.slider ul{
    display: flex;
    width: 200%;
    overflow-x: hidden;
    transition: 0.5s;
}

.slider ul li{
    list-style: none;
    width: 50%;
    padding: 20px;
}

.slide{
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.219);
    color: #676767;
    padding: 30px;
    border-radius: 10px;
}

.slide img{
    width: 65px;
    border-radius: 50%;
    margin-right: 10px;
    border: 4px solid #212EA0;
}

.user-info{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-size: 18px;
}

.user-info h3{
    color: #212EA0;
}

.slider p{
    font-size: 18px;
    line-height: 1.5;
}

@media (max-width : 800px){
    .testimonials{
        padding: 0 40px;
    }

    .next-btn, .back-btn{
        padding: 10px;
        width: 35px;
    }

    .testimonials ul li{
        padding: 10px;
    }

    .slide{
        padding: 10px;
    }

    .slide img{
        width: 50px;
        border: 3px solid #212EA0;
    }

    .user-info{
        font-size: 16px;
    }
}

@media(max-width : 600px){
    /* .user-info{
        margin-bottom: 10px;
        font-size: 12px;
    } */

    .user-info h3{
        font-size: 19px;
    }

    .slider p{
        font-size: 16px;
    }

    .slider ul{
        width: 400%;
    }
    
}