.programs-div{
    margin: 80px auto;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.program{
    flex-basis: 31%;
    position: relative;
}

.program img{
    width: 100%;
    border-radius: 10px;
    display: block;
}

.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 15, 152, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    color: white;
    opacity: 0;
    padding-top: 70%;
    transition: 0.5s;
}

.program:hover .caption{
    opacity: 1;
    padding-top: 0px;
}

.caption img{
    width: 60px;
    margin-bottom: 10px;
}

@media(max-width : 650px){
    .programs-div{
        flex-direction: column;
    }

    .program{
        flex-basis: 100%;
        margin: 20px;
    }
}